
import { ColumnMappingEditType } from './columnmapping-edittype';
import { GSPElementDataType } from '@gsp-bef/gsp-be-metadata';

export class ColumnMapping {
    /**
     * 内码
     */
    public ID = '';

    /**
     * 编号
     */
    public Code = '';
    /**
     * 名称
     */
    public Name = '';
    /**
     * 对象Id
     */
   public SrcDeoId = '';
    /**
     * 是否选择
     */
    public IsSelect: Boolean = false;
    /**
     * 数据类型
     */
    public DataType: GSPElementDataType = GSPElementDataType.String;
    /**
     * 执行顺序
     */
    public ExeOrder: number;
    /**
     * 目标列内码
     */
    public TargetElementID = '';

    /**
     * 展示映射表达式
     */
    public DisplayExpression: String = '';
    /**
     * 实际映射表达式
     */
    public Expression: String = '';
    /**
     * 映射编辑类型
     */
    public MappingType: ColumnMappingEditType;
}



