/**
* Mapping映射编辑类型
*/
export enum ColumnMappingEditType {
    /**
     * 常量
     */
    Constant = 0,
    /**
     * 字段映射
     */
    FieldMapping = 1,
    /**
     * 表达式
     */
    Expression = 2,
    /**
     * 系统变量
     */
    SysVariable = 3,
    /**
     * GUID，DIP唯一值
     */
    CreateGuid = 4,
    /**
     * 聚合
     */
    Aggregate = 5
}

export const MappingTypes: Array<{ mappingType: ColumnMappingEditType | String, name: string }> = [
    {
        mappingType: ColumnMappingEditType.Constant,
        name: '常量'
    },
    {
        mappingType: ColumnMappingEditType.FieldMapping,
        name: '字段映射'
    },
    {
        mappingType: ColumnMappingEditType.Expression,
        name: '表达式'
    },
    // {
    //     mappingType: ColumnMappingEditType.SysVariable,
    //     name: '系统变量'
    // },
    {
        mappingType: ColumnMappingEditType.CreateGuid,
        name: 'GUID'
    // },
    // {
    //     mappingType: ColumnMappingEditType.Aggregate,
    //     name: '聚合'
    }
];



