import { Type } from '@angular/core';

// 控件全部配置
export class ElementPropertyConfig {
    categoryId: string;
    categoryName: string;
    properties: PropertyEntity[];
    setPropertyRelates?(propertyID, propertyValue);
}

// 属性实体
export class PropertyEntity {
    propertyID: string; // 属性ID
    propertyName: string; // 属性显示的名称
    propertyType: string; // 属性的类型：String/Number/Boolean或自定义类型
    description?: string; // 属性描述
    defaultValue?: any; // 属性的默认值
    readonly?= false; // 是否只读
    visible?= true; // 是否可见
    iterator?: KeyMap[]; // 下拉框的枚举值
    editor?: Type<any>; // 属性自定义编辑器（以模态框的形式展示）
    editorParams?: any; // 自定义编辑器参数
    converter?: TypeConverter; // 属性值转换器
    [propName: string]: any;
}

// 转换器
export interface TypeConverter {
    convertTo(data: any): string; // 由模态框转为属性框中展示的值
}

export interface KeyMap {
    key: string;
    value: any;
}



