import { MappingUnit } from '../mapping-unit';
import { MappingRule } from '../mapping-rule';
export class MappingTree {
    /**
     * Id
     */
    public id = '';
    /**
     * 上级节点
     */
    public parentNode: MappingTree;
    /**
     * 节点层级
     */
    public level: number;
    /**
     * 显示映射单元(规则)编号
     */
    public code = '';
    /**
     * 显示映射单元(规则)名称
     */
    public name = '';
    /**
     * 显示源对象名称
     */
    public srcDeoName = '';
    /**
     * 显示目标对象名称
     */
    public tarDeoName = '';
    /**
     * 是否映射单元
     */
    public isUnit = false;
    /**
     * 当前树节点
     */
    public mappingNode: MappingRule | MappingUnit = new MappingRule();
    public units: MappingTree[];
    constructor() {

    }
}



